<template>
  <ul class="todo-list">
    <li
      :class="['todo-item', { completed: item.status && !optionsData.active }]"
      v-for="item in todoListData"
      :data-todo-id="item.id"
      @click.stop="statusChange"
      :key="item.id"
      v-show="
        item.isShow &&
        (options.all ||
          (options.active && !item.status) ||
          (options.completed && item.status))
      "
    >
      <div class="checkbox">
        <div class="i-active" v-if="!item.status"></div>
        <div class="i-completed" v-if="item.status"></div>
      </div>
      <p>{{ item.value }}</p>

      <div class="delete" @click="deleteItem">
        <svg
          class="svg-inline--fa fa-times fa-w-11 close"
          aria-hidden="true"
          focusable="false"
          data-prefix="fas"
          data-icon="times"
          role="img"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 352 512"
          data-v-132cabf7=""
        >
          <path
            class=""
            fill="currentColor"
            d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"
          ></path>
        </svg>
      </div>
    </li>
  </ul>
</template>
<script>
export default {
  props: ["todoList", "optionsData"],

  data() {
    return {
      todoListData: this.todoList,
      options: this.optionsData,
    };
  },
  watch: {
    todoList(newValue) {
      this.todoListData = newValue;
    },
    optionsData(n) {
      this.options = n;
    },
  },
  methods: {
    statusChange(e) {
      if (e.target.className == "delete") {
        return;
      }
      let id = e.path[1].dataset.todoId ?? e.path[2].dataset.todoId;

      this.$emit("current-todoList", id);
    },
    deleteItem(e) {
      console.log(e);
      let id = e.path[2].dataset.todoId ?? e.path[3].dataset.todoId;

      this.$emit("current-todoList-delete", id);
    },
  },
};
</script>