<template>
  <div class="form">
    <div class="title">
      <span>~ Today I need to ~</span>
    </div>
    <div class="input-container">
      <div class="input">
        <input
          type="text"
          v-model="inputData"
          @keydown.enter="addTodoItem"
          placeholder="Add new todo..."
        />
      </div>
      <button
        :class="[{ down: !btnstatus }]"
        @click="addTodoItem"
        @mousedown="btndown"
        @mouseup="btnup"
      >
        <span>Submit</span>
      </button>
    </div>
  </div>
</template>
<script>
export default {
  props: [],

  data() {
    return {
      inputData: "",
      btnstatus: false,
    };
  },
  methods: {
    addTodoItem() {
      if (!this.inputData.trim()) {
        return;
      }
      let newTodoItem = {
        value: this.inputData,
        status: false,
        id: new Date().getTime(),
        isShow: true,
      };
      this.$emit("current-add-todo", newTodoItem);
      this.inputData = "";
    },
    btndown(e) {
      this.btnstatus = true;
      e.path[1].style.transform = "rotate(0deg)";
      e.path[1].style.transform = "translateY(5px)";
      setTimeout(() => {
        if (this.btnstatus) {
          this.btnup(e);
        }
      }, 1000);
    },
    btnup(e) {
      this.btnstatus = false;
      e.path[1].style.transform = "translateY(-5px)";
      e.path[1].style.transform = "rotate(4deg)";
    },
  },
};
</script>