<template>
  <div id="app">
    <div class="header">
      <div class="logo">
        <img src="./assets/logo.svg" />
      </div>
      <span class="title"> To-Do List </span>
    </div>
    <TodoListHeaderComponent
      @current-add-todo="dealWithInputData"
    ></TodoListHeaderComponent>
    <TodoListMainComponents
      :todoList="todoListData"
      :optionsData="options"
      @current-todoList="statusChange"
      @current-todoList-delete="deleteItem"
    ></TodoListMainComponents>
    <TodoListFooterComponents
      :todoStatus="todoStatusList"
      :optionsData="options"
      @get-new-options="getNewOptions"
      @get-new-status="getNewStatus"
      @clean-completed="cleanCompleted"
    ></TodoListFooterComponents>
  </div>
</template>

<script>
import TodoListHeaderComponent from "./components/TodoListHeaderComponent";
import TodoListFooterComponents from "./components/TodoListFooterComponents";
import TodoListMainComponents from "./components/TodoListMainComponents.vue";
import "@/assets/fonts/font.css";
import "@/assets/index.css";
export default {
  name: "App",
  components: {
    TodoListHeaderComponent,
    TodoListMainComponents,
    TodoListFooterComponents,
  },
  data() {
    return {
      todoListData: [],
      todoStatusList: {
        hasTodoItem: 0,
        completed: 0,
        active: 0,
      },
      options: {
        all: true,
        active: false,
        completed: false,
      },
    };
  },
  methods: {
    initOptions() {
      this.options.all = true;
      this.options.active = false;
      this.options.completed = false;
    },
    dealWithInputData(data) {
      this.todoStatusList.hasTodoItem++;
      this.todoStatusList.active++;
      this.todoListData.push(data);
      this.initOptions();
    },
    statusChange(id) {
      for (let i = 0; i < this.todoListData.length; i++) {
        if (id == this.todoListData[i].id) {
          this.todoListData[i].isShow = false;
          this.todoListData[i].isShow = true;

          this.todoListData[i].status = !this.todoListData[i].status;
          if (this.todoListData[i].status) {
            this.todoStatusList.completed++;
            this.todoStatusList.active--;
          } else {
            this.todoStatusList.completed--;
            this.todoStatusList.active++;
          }
          break;
        }
      }
    },
    deleteItem(id) {
      this.todoListData = this.todoListData.filter(i => {
        if (i.id == id) {
          if (i.status) {
            this.todoStatusList.completed--;
          } else {
            this.todoStatusList.active--;
          }
          return false;
        } else {
          return true;
        }
      });
      this.todoStatusList.hasTodoItem--;
      if (
        this.todoStatusList.completed == 0 ||
        (this.todoStatusList.active == 0 && this.options.active)
      ) {
        this.initOptions();
      }
    },
    getNewOptions(n) {
      this.options = n;
    },
    getNewStatus(n) {
      this.todoStatusList = n;
    },
    cleanCompleted() {
      let num = 0
      this.todoListData = this.todoListData.filter((i) => {
        if (i.status) {
          num++;
        }
        return !i.status;
      });
      this.todoStatusList.hasTodoItem = this.todoStatusList.hasTodoItem - num;
      this.todoStatusList.active = this.todoStatusList.hasTodoItem;
      this.todoStatusList.completed = 0;
      this.initOptions()
    },
  },
};
</script>

