<template>
  <footer>
    <div class="empty" v-if="todoStatusList.hasTodoItem == 0">
      <div class="footer-logo">
        <svg
          class="svg-inline--fa fa-clipboard-check fa-w-12 icon"
          aria-hidden="true"
          focusable="false"
          data-prefix="fas"
          data-icon="clipboard-check"
          role="img"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 384 512"
          data-v-132cabf7=""
        >
          <path
            class=""
            fill="currentColor"
            d="M336 64h-80c0-35.3-28.7-64-64-64s-64 28.7-64 64H48C21.5 64 0 85.5 0 112v352c0 26.5 21.5 48 48 48h288c26.5 0 48-21.5 48-48V112c0-26.5-21.5-48-48-48zM192 40c13.3 0 24 10.7 24 24s-10.7 24-24 24-24-10.7-24-24 10.7-24 24-24zm121.2 231.8l-143 141.8c-4.7 4.7-12.3 4.6-17-.1l-82.6-83.3c-4.7-4.7-4.6-12.3.1-17L99.1 285c4.7-4.7 12.3-4.6 17 .1l46 46.4 106-105.2c4.7-4.7 12.3-4.6 17 .1l28.2 28.4c4.7 4.8 4.6 12.3-.1 17z"
          ></path>
        </svg>
      </div>
      <span>Congrat, you have no more tasks to do</span>
    </div>
    <div class="option" v-if="!todoStatusList.hasTodoItem == 0">
      <div class="item-count">
        {{ todoStatusList.hasTodoItem - todoStatusList.completed }} item left
      </div>
      <div
        :class="['all-item','o-item', { choose: options.all }]"
        @click="
          options.all = true;
          options.active = false;
          options.completed = false;
        "
      >
        All
      </div>
      <div
        :class="['active-item','o-item', { choose: options.active }]"
        v-if="todoStatusList.active != 0 && todoStatusList.completed != 0"
        @click="
          options.all = false;
          options.active = true;
          options.completed = false;
        "
      >
        Active
      </div>
      <div
        :class="['completed-item','o-item', { choose: options.completed }]"
        v-if="
          todoStatusList.completed != 0 &&
          todoStatusList.completed != todoStatusList.hasTodoItem
        "
        @click="
          options.all = false;
          options.active = false;
          options.completed = true;
        "
      >
        Completed
      </div>
      <div
        class="clear-c-item o-item"
        v-if="todoStatusList.completed != 0"
        @click="cleanCompleted"
      >
        Clear completed
      </div>
    </div>
  </footer>
</template>
<script>
export default {
  props: ["todoStatus", "optionsData"],

  data() {
    return {
      todoStatusList: this.todoStatus,
      options: this.optionsData,
    };
  },
  watch: {
    todoStatusList(n) {
      this.$emit("get-new-status", n);
    },
    options(n) {
      this.$emit("get-new-options", n);
    },
    todoStatus(n) {
      this.todoStatusList = n;
    },
    optionsData(n) {
      this.options = n;
    },
  },
  methods: {
    cleanCompleted() {
      this.$emit("clean-completed");
    },
  },
};
</script>